body {
  background-color: var(--bg-color);
  color: var(--bg-text);
  font-family: $font-poppins;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.75;
  letter-spacing: 0;
}
html,
body {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
blockquote,
cite,
p {
  margin: 0;
}

.list {
  list-style-type: none;
  padding-inline-start: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: $mobile) {
    max-width: $tablet;
  }

  @media screen and (min-width: $tablet) {
    max-width: $tablet;
    padding: 0 32px;
  }

  @media screen and (min-width: $desktop) {
    max-width: $desktop;
    padding: 0 15px;
  }
}
