.slider {
  cursor: pointer;
  border: 4px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  box-shadow: 0 0 0 2px #fd5103, 0 0 4px #fd5103;
}

input:checked + .slider {
  background-color: #fd5103;
}

.slider:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #fd5103;
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  transform: translateX(-24px);
}

@media screen and (min-width: 767px) {
